@use "styles/stylesFunctions.scss" as stylesFunctions;

.body {
  background-color: #f8f8f8;
  padding-top: 98px;
  padding-bottom: 208px;
}
.mainRow {
  row-gap: 70px;
}
.pageHeaders {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  h3,
  p {
    margin: 0;
    text-align: center;
  }
  h3 {
    @include stylesFunctions.fontStyles(70px, 52px, 600, #000000, #0000);
    width: 100%;
    font-size: calc(2.2rem + 1vw);
  }
  p {
    @include stylesFunctions.fontStyles(35px, 26px, 300, #000000, #0000);
    opacity: 0.5;
    max-width: 600px;
  }
}

.formRow {
  padding: 60px 50px;
  background: #ffffff 0% 0% no-repeat padding-box;
  box-shadow: 0px 1px 6px #00000029;
  border: 0.5px solid #707070;
  border-radius: 10px;
}

.spliterCol {
  position: relative;
  &::before {
    position: absolute;
    content: " ";
    width: 1px;
    height: 100%;
    background-color: #d2d2d2;
    top: 0;
    left: 35%;
  }
}
.Form {
  .innerFromRow {
    row-gap: 31px;
  }
  @include stylesFunctions.flexLayout(center, flex-start, column, 20px);
  h3,
  label,
  span,
  p {
    margin: 0;
  }
  h3 {
    @include stylesFunctions.fontStyles(45px, 34px, 600, #000000, #0000);
  }
  label {
    position: relative;
    width: 100%;
    a {
      text-decoration: underline;
      color: #ffaa03;
    }
    div {
      margin-top: 9px;
      height: 10px;
    }
    label {
      @include stylesFunctions.fontStyles(24px, 18px, 300, #000000, #0000);
      position: absolute;
      top: 25%;
      left: 5%;
      width: fit-content;
    }
  }

  input[type="password"],
  input[type="email"],
  input[type="text"],
  input[type="date"] {
    padding: 22px 31px;
    width: 100%;
    border-radius: 5px;
    border: solid 0.5px #d2d2d2;
    @include stylesFunctions.fontStyles(39px, 29px, 300, #000000, #0000);
  }
  input[type="date"] {
    text-align: right;
    color: hsl(0, 0%, 0%, 50%);
  }
  input[type="checkbox"] {
    margin: 7px 5px;
    @include stylesFunctions.fontStyles(24px, 18px, 300, #000000, #0000);
  }
  input[type="submit"] {
    background-color: #001736;
    border-radius: 5px;
    width: 100%;
    padding-top: 22px;
    padding-bottom: 22px;

    &:disabled {
      filter: grayscale(1);
      opacity: 0.5;
      cursor: not-allowed;
    }
    @include stylesFunctions.fontStyles(29px, 22px, 600, #ffffff, #0000);
  }
}

.otherOptions {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 15px 0;

  div {
    margin-top: -50px;
    background: #ffffff 0% 0% no-repeat padding-box;
    box-shadow: 0px 3px 6px #00000029;
    border-radius: 5px;
    opacity: 1;
    width: 80%;
    padding: 25px 43px;
    padding-bottom: 60px;
  }
  h3,
  p {
    margin: 0;
    width: 100%;
  }
  img {
    max-width: 100%;
    border-radius: 5px;
    opacity: 1;
    aspect-ratio: 480/320;
  }
  h3 {
    @include stylesFunctions.fontStyles(40px, 30px, 600, #000000, #0000);
    text-align: center;
    position: relative;
    padding-bottom: 25px;
    font-size: calc(1.2rem + 1vw);

    &::after {
      content: " ";
      position: absolute;
      right: 2%;
      bottom: 0%;
      width: 25%;
      height: 2px;
      background-color: #ffaa03;
    }
  }
  p {
    @include stylesFunctions.fontStyles(20px, 15px, 300, #000000, #0000);
    margin-top: 20px;
    color: #000000;
    text-align: center;
  }
  .signUp {
    width: 75%;
    padding-top: 20px;
    padding-bottom: 20px;
    border-radius: 5px;
    border: solid 1px #001736;
    text-align: center;
    @include stylesFunctions.fontStyles(25px, 24px, 600, #000000, #0000);
  }
  .loginOptions {
    @include stylesFunctions.flexLayout(center, center, row, 26px);
    img {
      width: 65px;
      aspect-ratio: 1/1;
    }
  }
}

@media screen and (max-width: 450px) {
  .formRow {
    padding: 20px 25px;
  }

  input[type="password"],
  input[type="email"],
  input[type="text"],
  input[type="date"] {
    padding: 10px 15px !important;

    font-size: 15px !important;
  }

  .otherOptions {
    display: none !important;
  }
}
